@font-face {
    font-family: 'Space Ranger Title';
    src: url(/tp/static/media/SpaceRangerTitle.8b44bfa5.woff2) format('woff2'),
    url(/tp/static/media/SpaceRangerTitle.a0e093f7.woff) format('woff');
    font-weight: normal;
    font-style: normal;
}

html, body {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    text-align: center;
    overflow-x: hidden;
}

body {
    color: #fff;
    background-color: #003333;
    font-family: 'Trebuchet MS', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    font-size: 1.3rem;
    font-weight: normal;
    font-style: normal;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

h1 {
    font-family: 'Space Ranger Title', monospace;
    font-weight: normal;
    font-style: normal;
    font-size: 1.5rem;
}

em {
    text-decoration: underline;
}

#root {
    position: fixed;
    width: 100%;
    padding: 0;
    z-index: 1;
}

#app, #scanner {
    position: absolute;
    width: 100%;
    height: 100%;
}

button {
    font-family: 'Space Ranger Title', monospace;
    box-shadow: 0 0 10px #00dcdc;
    background: #00bebe;
    color: #212121;
    font-size: 4vh;
    width: 100%;
    height: 10vh;
    border: none;
    padding: 1vh 0;
}

button:not(:last-child) {
    margin-bottom: 2vh;
}

button.not-important {
    color: #777777;
    background: #212121;
    box-shadow: none;
}
.dialog,  .dialog-info {
    width: 90%;
    padding-left: 5%;
    padding-right: 5%;
    height: 100%;
    margin-left: auto;
    margin-right: auto;
    overflow-y: scroll;
}

.dialog-info {
    position: relative;
    background-color: transparent;
}

.dialog-info::after {
    content: "";
    opacity: 0.5;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: fixed;
    z-index: -1;
    background-color: gray;
}

.dialog-inner {
    min-height: calc(100% - 2rem);
    display: flex;
    flex-direction: column;
    padding-top: 1rem;
    padding-bottom: 1rem;
}

.dialog-content {
    flex: 1 1;
    padding-bottom: 1rem;
}

@-webkit-keyframes highlight {
    0% {
        background: white;
    }
    100% {
        background: none;
    }
}

@keyframes highlight {
    0% {
        background: white;
    }
    100% {
        background: none;
    }
}

.dialog::after {
    content: "";
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    pointer-events: none;
    opacity: 0.25;
    -webkit-animation: highlight 0.5s;
            animation: highlight 0.5s;
}
.exploration {
    text-align: left;
}

.exploration h1 {
    padding: 0;
    margin: 0;
}
.detailed-planet-image-small {
    width: 25vw;
    height: 25vw;
}

.detailed-planet-image-medium {
    width: 37.5vw;
    height: 37.5vw;
}

.detailed-planet-image-big {
    width: 50vw;
    height: 50vw;
}

.star-system-details {
    text-align: left;
}

.star-system-details h1 {
    padding: 0;
    margin: 0;
}

.star-system-icon {
    vertical-align: middle;
    width: 2rem;
}

.celestial-info {
    border: 1px solid #00dcdc;
    display: flex;
    margin-bottom: 3vw;
    background-color: #002720;
}

.celestial-info.disabled {
    border: 1px solid #6e6e6e;
    background-color: #212121;
    opacity: 0.5;
}

.celestial-view {
    width: 7rem;
    height: 7rem;
    background-repeat: no-repeat;
    background-color: black;
    background-position: 50%;
    flex-shrink: 0;
}

.celestial-view.size-small {
    background-size: 50%;
}

.celestial-view.size-medium {
    background-size: 75%;
}

.celestial-view.size-big {
    background-size: 100%;
}

.celestial-actions {
    flex-shrink: 1;
    width: 100%;
    height: 7rem;
    display: flex;
    flex-direction: column;
    margin-left: 2vw;
    margin-right: 2vw;
}

.disabled .celestial-actions {
    vertical-align: middle;
    text-align: center;
    line-height: 7rem;
    color:gray;
    font-family: 'Space Ranger Title', monospace;
}

.celestial-action {
    font-size: 1.5rem;
    margin-top: 2vw;
    margin-bottom: 2vw;
    color:white;
    flex-grow: 1;
}

.celestial-action.action-conflict {
    background-color: #5d0100;
    box-shadow: 0 0 10px red;
}

.celestial-action.action-conflict::after {
    content: "CONFLICT";
    display: block;
    position: relative;
    font-size: 1rem;
    color: red;
    top: 0.5rem;
}

.celestial-action.action-exploration {
    background-color: #00254e;
    box-shadow: 0 0 10px #00a3ff;
}

.celestial-action.action-exploration::after {
    content: "EXPLORATION";
    display: block;
    position: relative;
    font-size: 1rem;
    color: #00a3ff;
    top: 0.5rem;
}

.celestial-action.action-movement {
    background-color: #004d00;
    box-shadow: 0 0 10px #00ff00;
}

.celestial-action.action-movement::after {
    content: "MOVEMENT";
    display: block;
    position: relative;
    font-size: 1rem;
    color: #00ff00;
    top: 0.5rem;
}
.data-log {
    padding-top: 1em;
    padding-bottom: 1em;
    width: 100%;
    background-color: #565656;
    margin: auto auto 2rem;
}

.data-log img {
    height: 30vh;
}

.icon-img {
    width: 1.5em;
    height: 1.5em;
    top: -0.25em;
    left: 0.0em;
    position: absolute;
}

.icon-inline {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    overflow: visible;
    height: 1em;
    width: 1.5em;
}
.scanning-planet-image {
    width: 2em;
    height: 2em;
}

.scanner-static-background, .scanner-locking-progress {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.scanner-static-background {
    position: absolute;
    background-image: url(/tp/static/media/scanner-interlace-01.b46b239c.png);
    background-size: 100% 100%;
    z-index: -1
}

.scanner-locking-progress {
    position: absolute;
    background-color: white;
    pointer-events: none;
    z-index: 10000;
}

.scanner-info {
    z-index: 10;
}

.scanner-overlay {
    z-index: 100;
}

.hidden-canvas {
    display: none;
}

.menu-button img {
    z-index: 200;
    width: 100%;
    height: 100%;
    font-size: 2em;
}

.scanner-info h1 {
    margin: 0;
    padding: 0;
    padding-top: 0.7rem;
    font-size: 2.0rem;
}

button.menu-button {
    z-index: 200;
    box-shadow: none;
    position: absolute;
    right: 0;
    width: 2.5rem;
    height: 2.5rem;
    padding: 0;
    margin: 0.3rem;

    background-color: transparent;
    border: none;
}

