button.planet-link {
    text-align: left;
    display:block;
    padding-left: 1rem;
    vertical-align: middle;
    color: white;
    height: auto;
}

.planet-link img {
    vertical-align: middle;
    width: 12vw;
    height: 12vw;
}

.universe-info .detailed-planet-image-small {
    width: 5vw;
    height: 5vw;
    vertical-align: middle;
}

.universe-info .detailed-planet-image-medium {
    width: 7.5vw;
    height: 7.5vw;
    vertical-align: middle;
}

.universe-info .detailed-planet-image-big {
    width: 10vw;
    height: 10vw;
    vertical-align: middle;
}
